import { Component, Injectable, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import { HttpService } from '../core/HttpService';

@Component({
  selector: 'app-cnt',
  templateUrl: './studentdata.component.html',
  styleUrls: ['./studentdata.component.css']
})

@Injectable()
export class StudentDataComponent implements OnInit {
  private _productUrl = 'http://localhost:85' + '/api/work/GetStudentResult';

  title = 'Student Data';
  displayedColumns = ['UserId', 'SubmittedAnswerId', 'SubmitDateTime', 'Correct', 'Progress',
    'ExerciseId', 'Difficulty', 'Subject', 'Domain','LearningObjective'];
  dataSource = new MatTableDataSource();
  errorMessage = '';


  constructor(private _http: HttpService) { }

  ngOnInit(): void {
    this.getStudentData()
      .subscribe(stData => this.dataSource = new MatTableDataSource(stData.Data),
        error => this.errorMessage = <any>error);
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }


  getStudentData(): Observable<ResponseElement> {
    return this._http.get(this._productUrl)
      .map((response: Response) => <ResponseElement>response.json())
      .catch(this.handleError);
  }
  private handleError(error: Response) {
    // in a real world app, we may send the server to some remote logging infrastructure
    // instead of just logging it to the console
    console.error(error);
    return Observable.throw(error.json().error || 'Server error');
  }
}


export class Element {
  UserId: number;
  SubmittedAnswerId: number;
  SubmitDateTime: Date;
  Correct: boolean;
  Progress: number;
  ExerciseId: number;
  Difficulty: number;
  Subject: string;
  Domain: string;
  LearningObjective: string;
}
export class ResponseElement {
  Data: Array<Element>;
  TotalCount: number;
}



