import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import {
  Http,
  RequestOptions,
  RequestOptionsArgs,
  Response,
  Request,
  Headers,
  XHRBackend
} from '@angular/http';

import { AngularReduxRequestOptions } from './AngularReduxRequestOptions';


@Injectable()
export class HttpService extends Http {

  apiUrl = "";
  requestList = [];

  constructor(
    backend: XHRBackend,
    defaultOptions: AngularReduxRequestOptions
  ) {
    super(backend, defaultOptions);
  }

  get(url: string, options?: RequestOptionsArgs): Observable<any> {
    


    return super.get(this.getFullUrl(url), this.requestOptions(options))
      .catch(this.onCatch)
      .do((res: Response) => {
        this.onSuccess(res);
      }, (error: any) => {
        this.onError(error);
      })
      .finally(() => {
      });

  }

  post(url: string, body: any, options?: RequestOptionsArgs): Observable<any> {
    
    return super.post(this.getFullUrl(url), body, this.requestOptions(options))
      .catch(this.onCatch)
      .do((res: Response) => {
        this.onSuccess(res);
      }, (error: any) => {
        this.onError(error);
      })
      .finally(() => {
      });

  }

  private requestOptions(options?: RequestOptionsArgs): RequestOptionsArgs {

    if (options == null) {
      options = new AngularReduxRequestOptions();
    }

    if (options.headers == null) {
      options.headers = new Headers();
    }

    options.headers.append('Access-Control-Allow-Headers', 'Access-Control-Allow-Origin,content-type,accept');
    options.headers.append('Access-Control-Allow-Origin', 'http://localhost:85');
    //options.headers.append('Content-Type', 'application/json; charset=UTF-8');

    return options;
  }

  private getFullUrl(url: string): string {
    return this.apiUrl + url;
  }

  private onCatch(error: any, caught: Observable<any>): Observable<any> {
    return Observable.throw(error);
  }

  private onSuccess(res: Response): void {
   // console.log('Request successful');
  }

  private onError(res: Response): void {
    //console.log('Error, status code: ' + res.status);
  }

  
}



